 .App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.list {
  text-align: left;
  max-width: 1000px;
  margin: auto;
}

.submit-form {
  max-width: 800px;
  margin: auto;
}

.edit-form {
  max-width: 800px;
  margin: auto;
}

html{
  font-size: 62.5%; /* 16px * 62.5 = 10px = 1rem */
  box-sizing: border-box;
}

body{
  font: 1.6rem Helvetica;
  height: 100vh;
  margin: 0;
  background-color: #eaeef3;
}

button{
  font-size: 1.6rem;
}
.full-width {
  width: 100%;
}
#root{
  height: 100%;
}
.grid-container{
  display: grid;
  grid-template-areas: 
  "header"
  "main"
  "footer";
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%;
}
.header{
  grid-area: header;
  background-color: #203040;
  color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
}
.brand a{
  color: #ffffff;
  font-size: 3rem;
  font-weight: bold;
  text-decoration: none;
}
.header-links a{
  color:#ffffff;
  /* color:blue; */
  text-decoration: none;
  padding: 1rem;

}
.header-links a:hover{
  /* color:#ff8000; */
  color: rgb(202, 97, 10);
}

.main{
  grid-area: main;
}
.footer{
  grid-area: footer;
  background-color: #203040;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}
a{
  text-decoration: none;
}
a:hover{
  color:#ff8000;
  /* color:darkblue; */
}
a:link{
  /* color:#ff8000; */
  /* Khi click an mouse vo link */
  color: blue;
}
a:visited{ 
  color:#ff8000;
  /* Da tung click vao */
  /* color:rgb(50, 50, 243); */
}
a:active{
  /* color:#ff8000; */
  /* khi click vo */
  color: blue;
}
/* Home Screen */
.products {
  display: table-row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap ;
}
.products li {
  list-style-type: none;
  padding: 0;
  flex: 1 3 24rem;
  margin: 1rem;
  height: 20rem;
  border-bottom: .2rem #c0c0c0 solid;
}
.product{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 100%;
}
.product-name{
  font-size: 1.5rem;
  font-weight: bold;
  color: #15045f;
}
.product-brand{
  font-size: 1.2rem;
  color:#808080;
}
.product-price{
  font-size: 2rem;
  font-weight: bold;
}
.product-image{
  max-width: 15rem;
  max-height: 15rem;
}
.product-rating{
  margin-bottom: 1rem;
}
/* Sidebar */

.brand button {
  font-size: 3rem;
  padding: .5rem;
  background: none;
  border: none;
  color:#ffffff;
  cursor: pointer;
}
.sidebar{
  position: fixed;
  transition: all .5s;
  transform: translateX(-30rem);
  width: 30rem;
  background-color: #f0f0f0;
  height: 100%;
}
.sidebar.open{
  transform: translateX(0);
}

.sidebar-close-button{
  border-radius: 50%;
  border: .1rem #000000 solid;
  width: 3rem;
  height: 3rem;
  padding: .5rem;
  font-size: 2rem;
  padding-top: 0;
  cursor: pointer;
  position: absolute;
  right: .5rem;
  top: 1.5rem
}

/* Product Details */
.details{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 1rem;
}
.details-image {
  flex: 2 1 30rem;
}
.details-image img{
  max-width: 60rem;
  width: 100%;
}
.details-info {
  flex: 1 1 30rem;
}
.details-action {
  flex: 1 1 30rem;
}

.details-info ul, .details-action ul{
  list-style-type: none;
  padding: 0;
}
.details-info li, .details-action li{
  margin-bottom: 1rem;
}
.back-to-result {
  padding: 1rem;
}
.details-action{
  border: .1rem #808080 solid;
  border-radius: 0.5rem;
  background-color: #f8f8f8;
  padding: 1rem;
}
.details-action ul li:last-child{
  display: flex;
  flex-direction: column;
}
.button{
  margin-top: 50rem;
  padding: 0.5rem;
  border: 0rem #808080 solid;
  border-radius: 0.5rem;
  cursor: pointer;
}
.button:hover{
  border: .1rem #c8f309 solid;
}
.button.primary{
  background-color: #3311f5;
}
.button.secondary{
  background-color: #f0f0f0;
}
.text-center{
  text-align: center;
}
/* Cart */

.cart{
  display: flex;
  flex-wrap: wrap;
  margin: 1rem;
  align-items: flex-start;
}
.cart-list{
  flex: 3 1 60rem;
}
.cart-action{
  flex:1 1 20rem;
  background-color: #f0f0f0; 
  border-radius: .5rem;
  padding: 0.5rem;
}
.cart-list-container {
  padding: 0;
  list-style-type: none;
  padding: 1rem;

}
.cart-list-container  li{
  display: flex;
  justify-content: space-between;

  padding-bottom: 0.1rem;
  margin-bottom: 0.1rem;
  border-bottom:.2rem #c0c0c0 solid;
}
.cart-list-container  li img{
  max-width: 10rem;
  max-height: 10rem;
}
.cart-list-container  li:first-child{
display: flex;
justify-content: space-between;
align-items: flex-end;
}
.cart-image{
  flex: 1 1;
}
.cart-name{
  flex: 8 1;
}
.cart-price{
  flex: 1 1;
  font-size: 2rem;
  text-align: right;
}

/* forms */

.form{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 2rem;
  margin-top: 2rem;
  /* border: .3rem #200258 solid; */
}
.form-container-big{
  height: 10%;
  display: flex;
  flex-direction: column;
  width:64rem;
  padding: 2rem;
  border: .1rem #c0c0c0 solid;
  border-radius: 0.5rem;
}
.form-container-big li{
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  margin-top: 1rem;
}
.form-container{
  display: flex;
  flex-direction: column;
  width:32rem;
  padding: 2rem;
  border: .2rem #16057a solid;
  border-radius: .9rem;
  list-style-type: none;
}
.form-container li{
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  margin-top: 1rem;
}
input{
  padding: 1rem;
  border: .1rem #c0c0c0 solid;
  border-radius: .5rem;
}


/* Products */

.product-header{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.content-margined{
  margin: 0.3rem;
}

.table{
  width: 100%;
}
th{
  text-align: left;
}
tbody>tr:nth-child(odd){
  background-color: #f0f0f0;
}

.searchInput {
  align-items: left;
  height: 50%;
  width: 25%;
}

.searchInputLong {
  align-items: left;
  height: 50%;
  width: 45%;
}

/* Checkout Steps */

.checkout-steps{
  display: flex;
  justify-content: space-between;
  width: 40rem;
  margin: 1rem auto;
 
}
.checkout-steps > div{
  border-top: .3rem #c0c0c0 solid;
  color: #c0c0c0;
  flex: 1 1;
  padding-top: 1rem;
}
.checkout-steps > div.active{
  border-top: .3rem #f08000 solid;
  color: #f08000;
}

/* Place Order */

.order-header{
  flex: 1 1 20rem;
  border: .1rem #c0c0c0 solid;
  border-radius: .5rem;
  background-color: #fcfcfc;
  padding: 1rem;
  margin: 0.5rem;
}

.order-footer{
  flex: 1 1 20rem;
  border: .1rem #c0c0c0 solid;
  border-radius: .5rem;
  background-color: #fcfcfc;
  padding: 1rem;
  margin: 0.5rem;
  text-align: center;
}

.order-info{
  flex: 1 1 20rem;
  background-color: #cbeaf7;
  padding: 0.5rem;
  margin: 0rem;
  margin-left: 1rem;
  justify-content: space-between;
}

.order-info-element{
  margin-left: 1rem;
}

.placeorder{
  display: flex;
  flex-grow: wrap;
  padding: 1rem;
  justify-content: space-between;
}
.placeorder-info{
  flex: 3 1 60rem;
  
}
.placeorder-detail{
  border: .1rem #c0c0c0 solid;
  border-radius: .5rem;
  padding: 1rem;
  margin: 0.5rem;
}

.placeorder-action{
  flex: 1 1 20rem;
  border: .1rem #c0c0c0 solid;
  border-radius: .5rem;
  background-color: #fcfcfc;
  padding: 1rem;
  margin: 0.5rem;
}
.placeorder-info > div{
  border: .1rem #c0c0c0 solid;
  border-radius: .5rem;
  background-color: #fcfcfc;
  padding: 1rem;
  margin: 1rem;
}
.placeorder-info > div:first-child{
  margin-top: 0;
}
.placeorder-action > ul {
  padding: 0;
  list-style-type: none;
}
.placeorder-action > ul > li{
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.placeorder-action > ul > li:last-child{
    font-size: 2rem;
    font-weight: bold;
    color: #c04000;
}
.placeorder-actions-payment > div {
  width: 100%;
  
}

/* Profile */

.profile{
  display: flex;
  flex-wrap: wrap;
}

.profile-info{
  flex: 1 1 30rem;
}
.profile-orders{
  flex: 3 1 60rem;
}

/* admin */
.dropdown{
  display: inline-block;
  line-height: 2.6rem;  
  /* position: relative; */
}
.dropdown-content{
  position: absolute;
  display: none;
  right:0;
  padding: 1.5rem;
  list-style-type: none;
  z-index: 1;
  background-color: #203040;
  margin:0.5rem;
  margin-top:0.5rem;
  width: 30rem ;
  text-align: right;
}
.dropdown:hover .dropdown-content{
  display: block;
}

/* filter */

.filter{
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 1rem auto;
  max-width: 40rem;
  justify-content: space-between;
  align-items: center;
}
.filter input,
.filter button,
.filter select{
  padding: 1rem;
  border-radius: 0.5rem;
  border:.1rem #c0c0c0 solid;
  font-size: 1.6rem;

}
.categories{

  padding: 0;
  list-style-type: none;
}
.categories a{
  display: flex;
  padding: 1rem;
}

.categories a:hover{
  background-color: #c0c0c0;
}

.page-link {
  margin: 2px;
  display: inline-block;
  padding: 2px;
  letter-spacing: 0.25rem;
  text-align: center;
  align-content: center;
  border-radius: 0.6rem;
  background-color: #0713c0;
  color:blue;
}

.current-link {
  background-color: #f7a40b;
  display: inline-block;
  color:blue;
  /* color: #e25a5a; */
  
}

.list-item {
  margin: 0.5rem;
  padding: 0.25rem;
  /* nth-child(even) {background: #CCC}; */
}

/* td > .list-item:nth-child(odd) {
  background-color: #CCC;
} */

.checkbox-dd {
  margin-right: 1.5rem;
  margin-top: 0.2rem;
  /* nth-child(even) {background: #CCC}; */
}

.avatar-image{
  max-width: 10rem;
  max-height: 10rem;
  align-items: center;
  justify-content: center;
  align-content: center;
  border-radius: 50%;
}

.avatarfile{
  max-width: 28rem;
  max-height: 10rem;
  align-content: center;
}
header.masthead {
  min-height: 6vh;
  height: 8vh;
  align-content: center;
}
.col-divide {
  margin: 0.5rem;
  padding: 0.25rem;
  color: #0713c0;
}

.col-divide-header {
  margin: 0.5rem;
  padding: 0.25rem;
  font-style: strong ;
  color: red;
}